* {
  box-sizing: border-box;
}

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: url("fonts/raleway-v22-latin-regular.woff2") format("woff2"), url("fonts/raleway-v22-latin-regular.woff") format("woff"), url("fonts/raleway-v22-latin-regular.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  src: url("fonts/raleway-v22-latin-500.woff2") format("woff2"), url("fonts/raleway-v22-latin-500.woff") format("woff"), url("fonts/raleway-v22-latin-500.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  src: url("fonts/raleway-v22-latin-600.woff2") format("woff2"), url("fonts/raleway-v22-latin-600.woff") format("woff"), url("fonts/raleway-v22-latin-600.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  src: url("fonts/raleway-v22-latin-700.woff2") format("woff2"), url("fonts/raleway-v22-latin-700.woff") format("woff"), url("fonts/raleway-v22-latin-700.ttf") format("truetype");
}
body {
  margin: 0;
  background: linear-gradient(0deg, #FAFAFA, #FAFAFA);
  font-family: "Raleway", sans-serif;
}

.bg {
  margin: 0 auto;
  text-align: center;
}

.container {
  max-width: 100%;
  width: 1366px;
  margin: 0 auto;
}

section.heading {
  text-align: center;
}
section.heading h1 {
  font-size: 40px;
  font-weight: 700;
}

section.animations {
  display: flex;
  align-items: center;
}
@media (max-width: 1024px) {
  section.animations {
    flex-direction: column;
  }
}

.stages {
  flex: 1 1 100%;
  max-width: 300px;
  margin-right: 40px;
}
.stages ul {
  padding-left: 0;
  list-style: none;
}
.stages ul li {
  width: 100%;
  height: 72px;
  margin-bottom: 1px;
  border: 1px solid #F0F0F0;
  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  cursor: pointer;
  border-radius: 12px 0 0 12px;
  font-weight: 700;
  color: #979797;
  user-select: none;
}
.stages ul li.active {
  background: #FFD300;
  color: #121212;
}
.stages ul li:hover {
  color: #121212;
}

#animation-helper {
  flex: 1;
  min-width: 720px;
}
@media (max-width: 1024px) {
  #animation-helper {
    min-width: 100%;
  }
}

#animation-container {
  width: 100%;
  padding-bottom: 62.708333333%;
  display: block;
  position: relative;
}
#animation-container img {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
}
#animation-container canvas {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#animation-container .animCont {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}